import React from 'react'
import classNames from 'classnames'
interface IconProps {
  type: string
  color?: string
  size?: 'xxs' | 'xs' | 'sm' | 'md' | 'lg' | 'xl'
  onClick?: () => void
  className?: string
  style?: React.CSSProperties
}
export default class Icon extends React.Component<IconProps, any> {
  constructor (props) {
    super(props)
    this.state = {
      urlsvg: null
    }
  }

  render () {
    const { className, style, type, size = 'xs', color, onClick } = this.props
    const cls = classNames(className, `yonui-icon-${size}`)
    return (
      <svg className={cls} style={{ ...style, color: color }} onClick={onClick}>
        <use xlinkHref={`#${type}`} />
      </svg>
    )
  }
}
